  justify-content: center;
  align-items: center;
  transition: all .5s;
  display: flex;
  position: relative;
}

.calendar__day:hover {
  cursor: pointer;
  background-color: var(--second-color);
  border: 1px solid var(--main-color);
  transform: translateY(-20px);
}

